


































































































































































































console.log("Load CriteriumCard.vue");
console.dir();

import * as Typ from "../types/index";
import {
  mdiAccount,
  mdiPencil,
  mdiShareVariant,
  mdiDelete,
  mdiAppleKeyboardControl,
  mdiContentSaveEdit,
  mdiFileRestoreOutline,
} from "@mdi/js";

import Vue from "vue";

export default Vue.extend({
  name: "CriteriumCard",

  //PROPS
  props: {
    propCriterium: {
      type: Object as () => Typ.criterium,
      default() {
        return {
          name: "",
          description: "",
          importance: Typ.criteriumImportance.undefined,
          isExclusionCriterium: false,
        } as Typ.criterium;
      },
    },
    propModuleState: {
      type: Object as () => Typ.criteriaModuleState,
      default: Typ.criteriaModuleState.increation as Typ.criteriaModuleState,
    },
    propEditState: {
      type: Object as () => Typ.editCriteriaModule,
      default: Typ.editCriteriaModule.increation as Typ.editCriteriaModule,
    },
    propUnsavedChanges: {
      type: Object as () => boolean,
      default: false as boolean,
    },
  },

  //METHODS
  methods: {
    isMinimized(): boolean {
      return this.moduleState === Typ.criteriaModuleState.minimized;
    },
    isInCreation(): boolean {
      return this.moduleState === Typ.criteriaModuleState.increation;
    },
    changeModuleState(state: string): void {
      //Log
      console.log(
        "CriteriumCard: change module state from '" +
          Typ.criteriaModuleState[this.moduleState] +
          "' to '" +
          state +
          "'"
      );

      var stateEnum = this.convertStringToModuleStateEnum(state);
      this.moduleState = stateEnum;

      if (this.moduleState === Typ.criteriaModuleState.increation) {
        this.editState = Typ.editCriteriaModule.increation;
      } else {
        this.editState = Typ.editCriteriaModule.none;
      }

      //Log
      console.log(
        "CriteriumCard: changed edit mode to '" +
          Typ.editCriteriaModule[this.editState] +
          "'"
      );
    },
    convertStringToModuleStateEnum(convert: string): Typ.criteriaModuleState {
      //Log
      console.log(
        "CriteriumCard: convert string '" + convert + "' to moduleStateEnum"
      );

      this.selectedDebugItem = convert;

      switch (convert) {
        case "minimized":
          return Typ.criteriaModuleState.minimized;
        case "maximized":
          return Typ.criteriaModuleState.maximized;
        default:
          return Typ.criteriaModuleState.increation;
      }
    },

    changeEditMode(mode: string): void {
      //Log
      console.log(
        "CriteriumCard: change edit mode from '" +
          Typ.editCriteriaModule[this.editState] +
          "' to '" +
          mode +
          "'"
      );

      var modeEnum: Typ.editCriteriaModule =
        this.convertStringToEditModeEnum(mode);

      if (this.editState === modeEnum) {
        modeEnum = Typ.editCriteriaModule.none;
      }

      this.editState = modeEnum;
    },
    canEdit(sender: string): boolean {
      //Log
      console.log(
        "CriteriumCard: request edit mode from '" +
          sender +
          "'; current: '" +
          Typ.editCriteriaModule[this.editState] +
          "'"
      );

      var senderEnum: Typ.editCriteriaModule =
        this.convertStringToEditModeEnum(sender);
      if (this.editState === Typ.editCriteriaModule.increation) {
        return true;
      }

      return senderEnum === this.editState;
    },
    convertStringToEditModeEnum(convert: string): Typ.editCriteriaModule {
      //Log
      console.log(
        "CriteriumCard: convert string '" + convert + "' to editEnum"
      );

      switch (convert) {
        case "increation":
          return Typ.editCriteriaModule.increation;
        case "name":
          return Typ.editCriteriaModule.name;
        case "description":
          return Typ.editCriteriaModule.description;
        case "importance":
          return Typ.editCriteriaModule.importance;
        case "isExclusionCriterium":
          return Typ.editCriteriaModule.isExclusionCriterium;
        default:
          return Typ.editCriteriaModule.none;
      }
    },

    updateImportance(importance: string): void {
      //Log
      console.log(
        "CriteriumCard: update criterium importance from '" +
          Typ.criteriumImportance[this.criterium.importance] +
          "' to '" +
          importance +
          "'"
      );

      var importanceEnum: Typ.criteriumImportance =
        this.convertStringToImportanceEnum(importance);
      this.criterium.importance = importanceEnum;
    },
    convertStringToImportanceEnum(convert: string): Typ.criteriumImportance {
      convert = convert.replaceAll(" ", "");

      //Log
      console.log(
        "CriteriumCard: convert string '" + convert + "' to importanceEnum"
      );

      switch (convert) {
        case "veryimportant":
          return Typ.criteriumImportance.veryimportant;
        case "important":
          return Typ.criteriumImportance.important;
        case "neutral":
          return Typ.criteriumImportance.neutral;
        default:
          return Typ.criteriumImportance.unimportant;
      }
    },
    convertImportanceEnumToString(convert: Typ.criteriumImportance): string {
      //Log
      console.log(
        "CriteriumCard: convert importanceEnum '" +
          Typ.criteriumImportance[convert] +
          "' to string"
      );

      switch (convert) {
        case Typ.criteriumImportance.veryimportant:
          return "very important";
        case Typ.criteriumImportance.important:
          return "important";
        case Typ.criteriumImportance.neutral:
          return "neutral";
        case Typ.criteriumImportance.unimportant:
          return "unimportant";
        default:
          return "";
      }
    },

    btnUpdate(): void {
      //LOG
      console.log("CriteriumCard: SAVE_UPDATE button clicked");

      this.changeEditMode("none");

      this.$emit("save_criterium", this.criterium);
    },
    btnRestore(): void {
      //LOG
      console.log("CriteriumCard: RESTORE button clicked");

      this.changeEditMode("none");

      this.criterium = this.restoreCriterium;

      this.$emit("restore_criterium");
    },

    validate(): boolean {
      return (this.$refs.form as Vue & { validate: () => boolean }).validate();
    },
    resetValidation() {
      (
        this.$refs.form as Vue & { resetValidation: () => void }
      ).resetValidation();
    },
    reset() {
      (this.$refs.form as Vue & { reset: () => void }).reset();
    },

    minAfterValidate() {
      if (this.validate()) {
        this.changeModuleState("minimized");
      }
    },
  },

  //DATA
  data() {
    return {
      criterium: JSON.parse(
        JSON.stringify(this.propCriterium)
      ) as Typ.criterium,
      restoreCriterium: {} as Typ.criterium,
      moduleState: this.propModuleState as Typ.criteriaModuleState,
      editState: this.propEditState as Typ.editCriteriaModule,

      importanceItems: [
        "very important",
        "important",
        "neutral",
        "unimportant",
      ] as string[],
      selectedImportance: (this.propCriterium.importance ===
      Typ.criteriumImportance.undefined
        ? ""
        : this.propCriterium.importance ===
          Typ.criteriumImportance.veryimportant
        ? "very important"
        : Typ.criteriumImportance[this.propCriterium.importance]) as string,

      rules: {
        required: (value: boolean | string) => !!value || "Required",
        str: [
          (val: string) => (val || "").length > 0 || "This field is required",
        ],
      },
      icons: {
        mdiAccount,
        mdiPencil,
        mdiShareVariant,
        mdiDelete,
        mdiAppleKeyboardControl,
        mdiContentSaveEdit,
        mdiFileRestoreOutline,
      },

      isValid: true as boolean,

      debug: false as boolean,
      debugItems: ["minimized", "maximized", "increation"] as string[],
      selectedDebugItem: "" as string,
    };
  },

  //WATCH
  watch: {
    propCriterium: {
      handler(newVal: Typ.criterium) {
        this.criterium = newVal;
        this.selectedImportance = this.convertImportanceEnumToString(
          this.criterium.importance
        );

        //LOG
        console.log("CriteriumCard: propCriterium changed!");
      },
      deep: true,
    },
    criterium: {
      handler(newVal: Typ.criterium) {
        this.$emit("update_criterium", newVal);
        this.selectedImportance = this.convertImportanceEnumToString(
          this.criterium.importance
        );

        //LOG
        console.log("CriteriumCard: criterium changed!");
      },
      deep: true,
    },
  },

  //MOUNTED
  mounted: function () {
    this.moduleState = this.propModuleState;
    this.editState = this.propEditState;
    this.selectedImportance = this.convertImportanceEnumToString(
      this.propCriterium.importance
    );

    this.resetValidation();

    //LOG
    console.log("CriteriumCard: Mounted");
  },
});
